import { ChangeDetectionStrategy, ChangeDetectorRef, Component } from "@angular/core";


import { Platform } from "ionic-angular";
import { App } from "ionic-angular";
import { NavController } from "ionic-angular";
import { NavParams } from "ionic-angular";

import { AppConstants } from "@app/app.constants";
import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsAuthService } from "@services/auth.service";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";

import { Backend } from "@backend/index";


@Component({
    selector: "page-promotion-categories",
    templateUrl: "promotion-categories.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PromotionCategoriesPage {

    navigationService: MarsNavigationService;
    translations: AppTranslations;

    subcategory: any;
    categories: Array<any>;
    merchant: string;
    promotionId: string;
    category: string;
    viewContentLoaded: boolean = false;
    spinner;

    constructor(private platform: Platform,
        private app: App,
        private changeDetector: ChangeDetectorRef,
        private navCtrl: NavController,
        private navParams: NavParams,
        private locales: AppLocales,
        private globals: AppGlobals,
        private authService: MarsAuthService,
        private interactionService: MarsInteractionService) {
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
        this.translations = this.locales.load();
    }

    ionViewCanEnter() {
        return this.navigationService.authCheck(PromotionCategoriesPage.name, ['merchant']);
    }

    ionViewWillEnter() {
        if (!MarsAuthService.hasRole('MERCHANT')) return;
        if (MarsAuthService.isLoggedIn()) {
            this.navParams.data.category = this.navParams.data.category || "root";
            this.category = this.navParams.data.category;
            this.promotionId = this.navParams.get("promotionId") == ":promotionId" ? undefined : this.navParams.get("promotionId");
            this.merchant = MarsAuthService.getLoggedInUser()._id;
            this.getMarketItemCategories();
        }
    }

    isActive(promotion) {
        return promotion && (promotion.status == "active");
    }

    async getMarketItemCategories() {
        try {
            let token = MarsAuthService.getMarsToken();
            let merchant = MarsAuthService.getLoggedInUser()._id;
            if (this.category == "root") {
                let details = (await Backend.getMerchantById({ id: merchant })).data;
                this.categories = details.inventory;
            } else {
                this.subcategory = (await Backend.getInventoryCategory({
                    merchant: this.merchant,
                    category: this.category,
                    xAccessToken: token
                })).data;
                this.categories = this.subcategory.children || [];
            }
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
            this.categories = [];
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    view(category: any) {
        if (category.children && category.children.length > 0)
            this.goToSubcategoriesPageFor(category);
        else
            this.goToCategoryItemsPageFor(category);
    }

    goToSubcategoriesPageFor(category: any) {
        this.navigationService.goTo("PromotionCategoriesPage", {
            promotionId: this.promotionId,
            category: category.slug
        });
    }

    goToCategoryItemsPageFor(category: any) {
        this.navigationService.goTo("PromotionItemsPage", {
            promotionId: this.promotionId,
            category: category.slug
        });
    }

    savePromotion() {
        this.interactionService.alert(this.translations.promotion_saved_successfully);
        this.navigationService.goToRootPage();
    }
}



